<template>
  <div style="background:#fff;" v-infinite-scroll="loadMore" infinite-scroll-immediate-check="true" infinite-scroll-disabled="infiniteDisabled" infinite-scroll-distance="80">

    <!-- <p class="item" v-for="(item,index) in list" :key="index">{{item}}</p> -->
    <Media v-for="(item,index) in list" :key="index" pic="data:image/png;base64," :title="`${item}`" desc=""></Media>

    <div style="min-height:80px; overflow: hidden;">
      <LoadMore v-if="status==''" tip="..." :showLoading="true"></LoadMore>
      <LoadMore v-if="status==''" tip="" :showLoading="false"></LoadMore>
      <LoadMore v-if="status==''" tip="" :showLoading="false" @click.native="loadMore"></LoadMore>
      <!-- <Nodata v-if="status==''" :imgurl="require('@src/assets/img/bg_empty_data.png')" content=''></Nodata> -->
    </div>
  </div>
</template>

<script>
// 
import infiniteScroll from '../directives/vue-infinite-scroll';
import LoadMore from "../components/v-load-more";
import Header from '../components/v-header';
import Media from "../components/v-media";
export default {
  directives: { infiniteScroll, },
  components: { LoadMore, Header, Media },
  data() {
    return {
      DB: [], // 
      status: "", // 
      query: {
        page: 1,
        size: 20
      },
      list: [],
    }
  },
  computed: {
    // true
    infiniteDisabled() {
      return this.status == '' || this.status == '' || this.status == '' || this.status == '';
    }
  },
  created() {
    this.initDB();
  },
  methods: {
    // 
    loadMore() {
      try {
        this.status = "";
        setTimeout(() => {
          let data = this.ajaxGetCollectList(this.query);
          this.setData(data);
        }, 500);
      } catch (error) {
        this.status = "";
      }
    },
    // 
    setData(data) {
      this.list = this.list.concat(data.collectList);
      // 
      if (this.list.length === 0) {
        this.status = "";
      } else if (this.query.page >= data.totalPages) {
        this.status = "";
      } else {
        this.status = "";
        this.query.page++;
      }
    },
    // 
    initDB() {
      for (let index = 0; index < 99; index++) {
        this.DB.push(index);
      }
    },
    // 
    ajaxGetCollectList(query) {
      console.log(query.page)
      let DB = [...this.DB];
      let collectList = DB.splice((query.page - 1) * query.size, query.size);
      return {
        collectList: collectList,
        totalPages: Math.ceil(this.DB.length / query.size),
      }
    },
  }

}
</script>

<style lang="less" scoped>
.item {
  line-height: 30px;
  text-align: center;
  padding: 10px;
  box-sizing: border-box;
  border-bottom: 1px solid #eee;
}
</style>



