
export default class Compress {
    constructor(file, callBack) {
        // super();
        // 
        this.reader = new FileReader();
        this.img = new Image();
        // 
        this.file = file;
        this.callBack = callBack;
        // canvas
        this.canvas = document.createElement('canvas');
        this.context = this.canvas.getContext('2d');
        this.init();
    }

    init() {
        this.img.onload = this.imgonLoad.bind(this);
        this.reader.onload = (e) => { this.img.src = e.target.result };
        // 
        if (this.file.type.indexOf("image") == 0) {
            this.reader.readAsDataURL(this.file);
        }
    }

    imgonLoad() {
        //canvas
        var canvas = document.createElement("canvas");
        var ctx = canvas.getContext('2d');
        // canvas
        var tCanvas = document.createElement("canvas");
        var tctx = tCanvas.getContext("2d");
        var maxsize = 100 * 1024;
        var initSize = this.img.src.length;
        var width = this.img.width;
        var height = this.img.height;



        //400
        var ratio;
        if ((ratio = width * height / 4000000) > 1) {
            ratio = Math.sqrt(ratio);
            width /= ratio;
            height /= ratio;
        } else {
            ratio = 1;
        }
        console.log("", ratio);
        console.log("width", width);
        console.log("height", height);

        // 
        var bili = 1;
        if (width > 480) {
            bili = 480 / width;
        } else {
            if (height > 640) {
                bili = 640 / height;
            } else {
                bili = 1;
            }
        }
        console.log('', bili);

        canvas.width = width;
        canvas.height = height;
        // 
        ctx.fillStyle = "#fff";
        ctx.fillRect(0, 0, canvas.width, canvas.height);

        //100
        var count;
        if ((count = width * height / 1000000) > 1) {
            count = ~~(Math.sqrt(count) + 1); //
            //
            var nw = ~~(width / count);
            var nh = ~~(height / count);
            tCanvas.width = nw;
            tCanvas.height = nh;
            for (var i = 0; i < 1; i++) {
                for (var j = 0; j < 2; j++) {
                    tctx.drawImage(this.img, i * nw * ratio, j * nh * ratio, nw * ratio, nh * ratio, 0, 0, nw, nh);
                    ctx.drawImage(tCanvas, i * nw, j * nh, nw, nh);
                }
            }
        } else {
            ctx.drawImage(this.img, 0, 0, width, height);
        }
        //
        var ndata = canvas.toDataURL('image/jpeg', bili);

        console.log('' + initSize);
        console.log('' + ndata.length);
        console.log('' + ~~(100 * (initSize - ndata.length) / initSize) + "%");
        tCanvas.width = tCanvas.height = canvas.width = canvas.height = 0;

        this.callBack(ndata);
    }
}


