  methods: {
    clickMenu(v) {
      console.log(v)
    },
    showToast() {
      this.$toast("")
    },
    iosAlert() {
      this.$iosAlert({
        title: "",
        text: "",
        input: false,
        placeholder: '',
        cancelText: '',
        okText: '',
        remindDuration: 650,
        appendChildClass: "",
      })
    },
    iosConfirm() {
      this.$iosConfirm({
        title: "",
        text: "",
        input: false,
        placeholder: '',
        cancelText: '',
        okText: '',
        remindDuration: 650,
        appendChildClass: "",
      })
    },
    iosPrompt() {
      this.$iosPrompt({
        title: "",
        text: "",
        input: false,
        placeholder: '',
        cancelText: '',
        okText: '',
        remindDuration: 650,
        appendChildClass: "",
      }).then(() => {
        this.iosRemind();
      })
    },
    iosRemind() {
      this.$iosRemind({
        title: "",
        text: "",
        input: false,
        placeholder: '',
        cancelText: '',
        okText: '',
        remindDuration: 650,
        appendChildClass: "",
      })
    },
    showMaskBox() {
      this.maskBox = this.maskBoxLoading = true;
      setTimeout(() => {
        this.maskBoxLoading = false;
      }, 1000);
    },
    dateTimeSelect1(val) {
      this.dateTime1 = val;
    },
    dateTimeSelect2(val) {
      this.dateTime2 = val;
    },
    dateTimeSelect3(val) {
      this.dateTime3 = val;
    },
    dateTimeSelect4(val) {
      this.dateTime4 = val;
    },
    dateTimeSelect5(val) {
      this.dateTime5 = val;
    },
    cityConfirm(v) {
      this.cityValue = `${v[0].name}-${v[1].name}-${v[2].name}`;
    },
    cityConfirm1(v) {
      this.defaultCityVal1 = v;
      if (v.length) this.cityValue1 = `${v[0].name}-${v[1].name}-${v[2].name}`;
    },
    cityConfirm2(v) {
      console.log(v)
      this.defaultCityVal2 = v;
      if (v.length) this.cityValue2 = `${v[0].name}-${v[1].name}-${v[2].name}`;
    },
    inputFileChange1(event) {
      let input = event.target;
      var file = input.files[0];

      // 4M
      if (file.size > 4 * 1024 * 1024) {
        this.$toast("4M");
        input.value = "";
        return;
      }

      if (!/image\/\w+/.test(file.type)) {
        this.$toast("");
        input.value = "";
        return;
      }

      // 100kb 
      if (file.size < 100 * 1024) {
        let reader = new FileReader();
        reader.onload = (e) => {
          let base64 = e.target.result;
          this.clipper(base64);
        }
        reader.readAsDataURL(file);
      } else {
        lrz(file, { width: 750 }).then((result) => {
          this.clipper(result.base64);
        })
      }
    },
    clipper(base64) {
      new Clipper({
        image_src: base64,
        ok: (base64, canvas) => {
          this.base641 = base64;
        },
        cancel: () => { },
        ok_text: "",
        cancel_text: ""
      });
    },
    inputFileChange2(event) {
      var file = event.target.files[0];
      lrz(file).then((result) => {
        new AlloyCrop({
          image_src: result.base64,
          circle: false, // optional parameters , the default value is false
          width: 200, // crop width
          height: 200, // crop height
          output: 2, // output resolution  --> 400*200
          ok: (base64, canvas) => {
            this.base642 = base64;
          },
          cancel: () => { },
          ok_text: "",
          cancel_text: ""
        });
      });
    },
    inputFileChange3(event) {
      var file = event.target.files[0];
      lrz(file).then((result) => {
        new AlloyCrop1({
          image_src: result.base64,
          circle: true, // optional parameters , the default value is false
          width: 200, // crop width
          height: 200, // crop height
          output: 1, // output resolution  --> 400*200
          ok: (base64, canvas) => {
            this.base643 = base64;
          },
          cancel: () => { },
          ok_text: "",
          cancel_text: ""
        });
      })
    },
    confirmPay() {

    },
    keyboardPopupConfirm(number) {
      setTimeout(() => {
        if (number == "637031") {
          this.$iosRemind({
            title: "",
            text: "",
            remindDuration: 2000,
            appendChildClass: "",
          })
          this.$refs.KeyboardPopup.close();
        } else {
          this.$refs.KeyboardPopup.error("")
        }
      }, 2000);
    },
  }
}
</script>

