  display: inline-block;
  border-radius: 50%;
  line-height: 40px;
  text-align: center;
  margin: 0 2px;
}
.checker-item-selected {
  border: 1px solid red;
}
.checker-item-disabled {
  background: #efefef;
}
.text-scroll {
  border: 1px solid #ddd;
  border-left: none;
  border-right: none;
}
.text-scroll p {
  background: #fff;
  font-size: 12px;
  text-align: center;
  line-height: 30px;
}
.goods-list {
}
.header-fix {
  position: fixed !important;
  width: 100%;
  top: 0;
  left: 0;
  background: #fafafa;
  zoom: 1;
  z-index: 1;
}
.infinite-scroll-box {
  height: 300px;
  overflow-x: hidden;
  overflow-y: auto;
}
.infinite-scroll-box-MaskBox {
  background: #fff;
  width: 300px;
  height: 300px;
  overflow-x: hidden;
  overflow-y: auto;
}
</style>



