<template>
  <div class="Keyboard-box" :class="{fixed:fixed,show:show}">
    <div class="numbers">
      <button class="_av" v-for="(item,index) in numbers" :key="index" :class="{'full':index == 9,'full-all':index == 9 && type=='number'}" @touchend="handleNumber($event,item)" type="button">{{item.name}}</button>
    </div>
    <div class="btns">
      <button @touchstart="touchstart($event)" @touchend="touchend" class="delete _av" type="button">delete</button>
      <button @click="confirm" class="pay-btn" :class="{disable:payDisable}" type="button">{{confirmText}}</button>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    type: {
      type: String,
      default: "number" // money & number
    },
    value: {
      type: [String, Number],
      default: ""
    },
    maxMoney: {
      type: Number,
      default: 99999
    },
    fixed: {
      type: Boolean,
      default: false
    },
    confirmText: {
      type: String,
      default: ""
    },
    maxLength: {
      type: String,
      default: "6"
    }
  },
  data() {
    return {
      numbers: [
        { name: "1" },
        { name: "2" },
        { name: "3" },
        { name: "4" },
        { name: "5" },
        { name: "6" },
        { name: "7" },
        { name: "8" },
        { name: "9" },
        { name: "0" },
        // { name: "." },
      ],
      values: [],
      btnDisable: false,
      payDisable: false,
      show: false
    }
  },
  mounted() {
    this.values = String(this.value).split("");
    this.init();
  },
  watch: {
    value(val) {
      this.values = String(val).split("");
    },
    values(arr) {
      let money = arr.join("");
      this.$emit("input", this.isMoneyRule(money));
    }
  },
  methods: {
    init() {
      //  type money
      this.type === "money" && this.numbers.push({ name: "." });
    },
    handleNumber(e, item) {
      e.preventDefault();
      let number = item.name;
      if (this.type === "money") {
        if (this.btnDisable) return; // push
        if (number === "." && this.values.indexOf(number) !== -1) return;// push
        if (Number(this.values.join("") + number) > this.maxMoney) return;// push
        if (this.values.join("") === "0") this.handleDelete();//0  0  0 
        this.values.push(number);
      } else {
        if (this.values.length >= this.maxLength) return;// push
        this.values.push(number);
      }
    },
    isMoneyRule(money) {
      if (this.type === "money") {
        if (money === '.') this.values.unshift("0") // .   0.
        this.payDisable = !Boolean(Number(money)) //  0  0.0  0.00
        this.btnDisable = (money.split(".")[1] || "").length >= 2; //>= 
        // let floatlast = money.split(".")[1];
        // floatlast && (this.btnDisable = floatlast.length >= 2);//>= 
        return money;
      } else {
        this.payDisable = this.values.length < this.maxLength // 
        return money;
      }
    },
    handleDelete() {
      this.values.pop();
    },
    touchstart(e) {
      this.timeOutEvent = setTimeout(this.longPress, 500);
      e.preventDefault();
    },
    touchmove() {
      clearTimeout(this.timeOutEvent);
      this.timeOutEvent = 0;
    },
    touchend() {
      clearTimeout(this.timeOutEvent);
      if (this.timeOutEvent != 0) {
        this.handleDelete();
      }
      return false;
    },
    longPress() {
      this.timeOutEvent = setInterval(() => {
        this.handleDelete();
      }, 100)
    },
    confirm() {
      this.payDisable || this.$emit('confirm');
    },
    open() {
      this.show = true;
    },
    close() {
      this.show = false;
    },
    // 
    disorderFn() {
      let arr = [...this.numbers];
      let newArr = [];
      while (arr.length) {
        let j = Math.floor(Math.random() * arr.length);
        newArr.push(arr[j]);
        arr.splice(j, 1);
      }
      this.numbers = newArr;
    }
  }
}
</script>


<style lang="less" scoped>
.fixed {
  position: fixed;
  width: 100%;
  left: 0;
  bottom: -100%;
}

.show {
  bottom: 0;
}

.Keyboard-box {
  background-color: #fff;
  display: flex;
  overflow: hidden;
  transition: bottom 0.3s linear;

  .numbers {
    flex: 1;
  }

  .btns {
    width: 25%;
  }

  button {
    float: left;
    width: 33.3333333333%;
    background: none;
    border: 0;
    height: 62px;
    line-height: 62px;
    text-align: center;
    font-size: 24px;
    border-right: 1px solid #d9d9d9;
    border-top: 1px solid #d9d9d9;
    touch-callout: none;
  }

  .full {
    width: 66.6666666666%;
  }
  .full-all {
    width: 100%;
  }

  .delete {
    font-size: 0;
    line-height: 0;
    overflow: hidden;
    height: 124px;
    width: 100%;
    border-right: 0;

    &::before {
      background: url(data:image/svg+xml;base64,);
      background-size: 32px auto;
      background-repeat: no-repeat;
      background-position: center center;
      content: "";
      display: block;
      width: 100%;
      height: 124px;
    }
  }

  .pay-btn {
    line-height: 1.2;
    height: 124px;
    background-color: #ff4242;
    color: #fff;
    width: 100%;
    border-right: 0;
    padding: 0 10px;
  }

  .disable {
    background-color: #aaa;
  }
}
</style>






