<template>
  <div class="loadmore-page">
    <div class="header" ref="header">
      <Header></Header>
      <div>
        
        {{query}}
      </div>
      <button @click="testClick('')"> </button>
      <button @click="testClick('')"> </button>
    </div>
    <div class="content">
      <ScrollRefreshBox :top="0" :api="ajaxGetCollectList" @watchDataList="watchDataList" :query="query" ref="scrollLoadmoreBox">
        <Media v-for="(item,index) in list" :key="index" pic="data:image/png;base64," :title="`${item}`" desc=""></Media>
      </ScrollRefreshBox>
    </div>
  </div>
</template>

<script>
import ScrollRefreshBox from '../components/v-scroll-refresh-box/index.vue';
import Header from '../components/v-header';
import Media from "../components/v-media";
export default {
  components: { ScrollRefreshBox, Header, Media },
  data() {
    return {
      DB: [], // 
      status: "", // 
      top: 0,
      list: [],
      query: { page: 1, limit: 20, type: "" }
    }
  },
  created() {
    this.initDB();
  },
  mounted() {
    this.setTop();
  },
  methods: {
    setTop() {
      this.top = this.$refs.header.offsetHeight;
    },
    watchDataList(list) {
      this.list = list;
    },
    testClick(type) {
      this.query["type"] = type;
      this.$refs.scrollLoadmoreBox.triggerPullToRefresh();
    },
    // 
    initDB() {
      for (let index = 0; index < 99; index++) {
        this.DB.push(index);
      }
    },
    // 
    ajaxGetCollectList(query) {
      console.log({ ...query })
      let DB = [...this.DB]
      let collectList = DB.splice((query.page - 1) * query.limit, query.limit);
      return {
        collectList: collectList,
        totalPages: Math.ceil(this.DB.length / query.limit),
      }
    },
  }
}
</script>
<style lang="less" scoped>
.loadmore-page {
  background: #fff;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  position: absolute;
}
.header {
}
.content {
  flex: 1;
  position: relative;
  height: 100%;
}
.item {
  line-height: 30px;
  text-align: center;
  padding: 10px;
  box-sizing: border-box;
  border-bottom: 1px solid #eee;
}
</style>



